import uniSearchBar from '../../components/uni-ui/uni-search-bar/uni-search-bar.vue'
export default {
	name: "",
	components: {
		uniSearchBar
	},
	props: {},
	data() {
		return {
			keywords: '', //搜索框的值
			searchHot: [], //搜索页热门

		}
	},
	methods: {
		//搜素事件
		search(e) {
			this.getSearchHot()
			// console.log(11);
		},
		input(e) { //
			this.keywords = e.value
			// console.log(this.keywords);
			// this.getSearchHot()
		},
		//获取搜素页热门预告
		getSearchHot() {
			let page = '1';
			let pageSize = 20;
			uni.request({
				url: `${this.$api}/search/list?qq=122212489&page=1&pageSize=${pageSize}&keywords=${this.keywords}`,
				method: 'POST',
				data: {},
				success: res => {
					this.searchHot = res.data.data.rows;
					// console.log(this.searchHot)
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//去单个热门详情
		toItemDetai(e) {		
			uni.navigateTo({
				url: `/pages/search/hotDetails/hotDetails?id=`+e.id
			})
			console.log(e)
		},
		
	},

	mounted() {

	},
	onLoad() {
		this.getSearchHot()
	},
	filters: {

	},
	computed: {

	},
	watch: {

	},
	directives: {

	}
}
